import React from "react";
import "./HeroSection.css";
import { Button } from "./Button";
import { Link as Nazdad } from "react-router-dom";
import Navbar from "./Navbar";
import Karta from "../4.png";

function HeroSection({
  lightBg,
  topLine,
  lightText,
  lightTextDesc,
  headline,
  description,
  buttonLabel,
  img,
  alt,
  imgStart,
}) {
  return (
    <>
      <Navbar />
      <div
        id="jol"
        className={lightBg ? "home__hero-section" : "home__hero-section darkBg"}
      >
        <div className="container">
          <div
            className="row home__hero-row"
            style={{
              display: "flex",
              flexDirection: imgStart === "start" ? "row-reverse" : "row",
            }}
          >
            <div className="col">
              <div className="home__hero-text-wrapper">
                <div className="top-line">{topLine}</div>
                <h1 className={lightText ? "heading" : "heading dark"}>
                  {headline}
                </h1>
                <p
                  className={
                    lightTextDesc
                      ? "home__hero-subtitle"
                      : "home__hero-subtitle dark"
                  }
                >
                  {description}
                </p>
              </div>
            </div>
            <div className="col">
              <div className="home__hero-img-wrapper">
                <img src={img} alt={alt} className="home__hero-img" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div id="jola" className="two-section">
        <div className="container">
          <div className="title">О ресторане</div>
          <div className="opisanie">
            Запоминающийся интерьер, панорамные окна,
            <br /> уютная и теплая атмосфера,
            <br />
            качественная музыка, самая вкусная кухня,
            <br /> демократичные цены, приветливый персонал.
            <br /> Все у нас поможет вам отлично провести время
            <br />
            со своими близкими.{" "}
          </div>
        </div>
      </div>
      <div id="jolaa" className="menusection">
        <div className="title container">
          <div>Меню</div>
          <div className="knopka">
            <Nazdad to="/menu">
              <button className="buttonknopka">Роллы</button>
            </Nazdad>
          </div>
          <div className="knopka2">
            <Nazdad to="/pizza">
              <button className="buttonknopka2">Пицца</button>
            </Nazdad>
          </div>
          <div className="knopka3">
            <Nazdad to="/menubarnoe">
              <button className="buttonknopka2">Барное-Меню</button>
            </Nazdad>
          </div>
        </div>
      </div>
      <div id="jolaaaa" className="сontacti ">
        <div className="container">
          <div className="titletwo ">Тц «Императрица»</div>
          <div className="opisanietwo">
            Адрес: Карла Маркса, 37
            <br /> Время работы: С 9:00 - До 23:00(пн-вс)
            <br />
            Телефон: +7-961-022-0220
            <br /> Доставка: С 10:00 - До 22:00(пн-вс)
          </div>
        </div>
      </div>
      <div className="karta">
        <a href="https://yandex.ru/maps/-/CCUiJ0rPdC">
          <img src={Karta} className="img1 container" />
        </a>
      </div>
    </>
  );
}

export default HeroSection;
