import React from "react";
import "../App.css";
import Navbarmenu from "./Navbarmenu";
import ImageSlider from "./ImageSlider";
import { SliderData } from "./SliderData";

function Menu() {
  return (
    <>
      <Navbarmenu />
      <ImageSlider slides={SliderData} />
    </>
  );
}

export default Menu;
